import * as Yup from 'yup';

export const ReviewSchema = Yup.object().shape({
  userName: Yup.string()
    .min(2, '  2 !')
    .max(20, '  20 !')
    .required(' '),
  rating: Yup.string()
    .required(' '),
  advantage: Yup.string()
    .min(3, '  3 !')
    .max(30, '  30 !')
    .required(' '),
  disadvantage: Yup.string()
    .min(3, '  3 !')
    .max(30, '  30 !')
    .required(' '),
  comment: Yup.string()
    .min(20, '  20 !')
    .max(1000, '  1000 !')
    .required(' '),
});
